import styled from "styled-components";

export const BlockContainer = styled("section")`
  position: relative;
  width: 100%;
  height: 100vh;

  margin-right: auto;
  margin-left: auto;
  padding: 0% 5%;
  
  background: #111;

  @media only screen and (max-width: 992px) {
    height: 100%;
    text-align: center;
  }
`;

export const ContentContainer = styled("section")`
  width: 100%;
  height: 100%;

  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;

  @media only screen and (max-width: 992px) {
    padding: 30% 0 30% 0;
  }
  @media only screen and (max-width: 768px) {
    padding: 50% 0 50% 0;
  }
`;

export const ContentWrapper = styled("div")`
  justify-content: center;
  align-items: center;
  display: flex;
  
  min-height: 500px;
`;

export const CardWrapper = styled("div")`
  margin-right: 2rem;
  
  @media only screen and (max-width: 992px) {
    margin: 0;
    padding: 0 0 40% 0;
  }
`;

export const CardTextTitle = styled("section")`
  background: -webkit-linear-gradient(25deg, #FCFCA4, #B0514F);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;

  font-family: BalooBold;
  font-size: 52px;
  line-height: 45px;
`;

export const CardText = styled("section")`
  color: #EEE;
  font-family: BalooMedium;
  font-size: 30px;
  line-height: 35px;
`;

export const Cube = styled("section")`
  position: absolute;
  background: #333;
  width: 80%;
  aspect-ratio: 1;
  transform: rotate(80deg);
  z-index: -1;
`;

export const ReduceLum = styled("section")`
  position: absolute;
  background: rgba(20, 20, 20, 10%);
  width: 80%;
  aspect-ratio: 1;
  transform: rotate(80deg);
  z-index: 1;
`;