import { Row, Col } from "antd";
import { Fade } from "react-awesome-reveal";
import { ImgPers } from "../../common/Img";

import {
  ContentContainer,
  BlockContainer,
  ContentWrapper,
  CardTextTitle,
  CardText,
  CardWrapper,
  Cube,
  ReduceLum,
} from "./styles";

const History = () => {

  return (
    <BlockContainer>
      <ContentContainer>

        <Row>

          <Col lg={12} md={24} sm={24} xs={24} offset={0}>
            <Fade delay={200}>
                <ContentWrapper>
                  <CardWrapper>
                    <CardTextTitle>A unique NFT stored on Solana</CardTextTitle>
                    <CardText>
                      <br /> Each Teddy Bears is different in its own way, some more rare than others.
                      <br /><br /> But one thing's guaranteed, there will only ever be 1,111 Teddys in the world and only you can hold it !</CardText>
                  </CardWrapper>
                </ContentWrapper>
            </Fade>
          </Col>

          <Col lg={12} md={24} sm={24} xs={24} offset={0}>
            <Fade delay={400}>
                <ContentWrapper>
                  <div style={{ transform: "rotate(5deg)", justifyContent: "center",  alignItems: "center", display: "flex" }} >
                    <ReduceLum style={{ transform: "rotate(0deg)" }} />
                    <ImgPers src="Tedgif.gif" width="80%" height="80%" />
                  </div>
                  <Cube style={{ transform: "rotate(14deg)", background: "#222" }} />
                  <Cube style={{ transform: "rotate(11deg)", background: "#333" }} />
                  <Cube style={{ transform: "rotate(8deg)", background: "#444" }} />
                </ContentWrapper>
            </Fade>
          </Col>

        </Row>

      </ContentContainer>
    </BlockContainer>
  );
};

export default (History);
